.quiz {
    padding-top: 50px;
}

.quiz__row {
    background-color: #4157A2;
    text-align: center;
    line-height: 25px;
}

.quiz__title {
    font-weight: normal;
    white-space: pre-line;
    font-size: 30px;
    padding: 30px 0 0;
    margin: 0 20px;
}

.quiz__text {
    font-size: 25px;
    padding-bottom: 30px;
    margin: 10px 0 0 0;
}

.quiz__buttonContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-position: center top ;
    background-size: contain;
    min-height: 640px;
    position: relative;
}

.quiz__button {
    min-height: 50vw;
    position: absolute;
    top: 14vw;
}

.button__image {
    align-self: center;
    transition: all 1s 0.1s ease-in;
    height: 25vw;

}

.button__image:hover {
    transform: scale(1.5) rotate(360deg) ;
}

@media screen and (max-width: 1100px) {
    .quiz__text {
        font-size: 25px;
    }

    .quiz__button {
        top: 18vw;
    }

    .button__image {
        height: 30vw;
    }
}

@media screen and (max-width: 900px) {
    .quiz__title {
        font-size: 25px;
    }

    .quiz__text {
        font-size: 20px;
    }

    .quiz__button {
        top: 25vw;
    }

}

@media screen and (max-width: 850px) {
    .button__image {
        height: 40vw;
    }
}

@media screen and  (max-width: 600px) {
    .quiz__buttonContainer {
        min-height: 500px;
    }
    .button__image {
        height: 50vw;
    }
}

@media screen and (max-width: 440px) {
    .quiz__buttonContainer {
        min-height: 400px;
    }

    .quiz__title {
        font-size: 25px;
    }

    .main__text {
        font-size: 15px;
    }
}

@media screen and (max-width: 360px) {
    .quiz__buttonContainer {
        min-height: 300px;
    }

    .quiz__title {
        font-size: 20px;
    }

    .quiz__text {
        font-size: 15px;
    }

    .main__text {
        font-size: 10px;
    }
}

