.secondDay {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.secondDay__row {
    width: 100%;
    background-color: #4157A2;
    text-align: center;
    border-top: 3px solid #8c9cd1;
}

.secondDay__title {
    font-size: 40px;
    font-weight: normal;
    margin: 20px;
}

.secondDay__text {
    max-width: 1300px;
    font-size: 30px;
    margin: 0 20px;
}

.secondDay__secret {
    text-align: center;
}

.secondDay__postScriptum {
    font-size: 25px;
}

.secondDay__tel {
    text-decoration: none;
    color: white;
    transition: opacity .3s linear;
}

.secondDay__tel:hover {
    color: #059f19;
    opacity: .6;
}

@media screen and (max-width: 1100px) {
    .secondDay__text {
        font-size: 25px;
    }
}


@media screen and (max-width: 940px){
    .secondDay__title {
        font-size: 30px;
    }

    .secondDay__text {
        font-size: 20px;
    }
}

@media screen and (max-width: 440px) {
    .secondDay__title {
        font-size: 25px;
    }

    .secondDay__text {
        font-size: 15px;
    }

    .secondDay__postScriptum {
        font-size: 15px;
    }
}
