.nav {
    display: flex;
    width: 90%;
    justify-content: space-between;
    list-style: none;
    padding: 24px 0;
    align-self: center;
}

.link {
    color: white;
    text-decoration: none;
    transition: opacity .3s linear;
}


.link:hover {
    color: #059f19;
    opacity: .6;
}

@media screen and (max-width: 700px) {
    .nav {
        display: none;
    }
}

