
.footer {
    width: 100%;
    background-color: #4157A2;
    text-align: center;
}

.footer__link {
    text-decoration: none;
    color: white;
    font-size: 30px;
    transition: opacity .3s linear;
}

.footer__link p {
    display: block;
    margin: 0;
    padding: 20px 0;
}
.footer__link:hover {
    opacity: .6;
    color: #059f19;
}


@media screen and (max-width: 1100px) {
    .footer__link {
        font-size: 25px;
    }
}


@media screen and (max-width: 940px){
    .footer__link {
        font-size: 30px;
    }

}

@media screen and (max-width: 440px) {
    .footer__link {
        font-size: 20px;
    }
}
