.header{
    display: flex;
    background-color: #4157A2;
    flex-direction: column-reverse;
    font-size: 30px;
}



.button {
    display: none;
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
    width: 24px;
    height: 24px;
    margin-right: 10px;
    align-self: flex-end;
    transition: opacity .3s linear;
}

.button:hover {
    opacity: .6;
}

@media screen and (max-width: 1100px) {
    .header {
        font-size: 25px;
    }
}

@media screen and (max-width: 900px) {
    .header {
        font-size: 20px;
    }
}

@media screen and (max-width: 700px) {
    .button {
        display: block;
    }

    .nav {
        display: none;
    }
}
