.location {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.location__row {
    width: 100%;
    background-color: #4157A2;
    text-align: center;
}

.location__title {
    font-size: 40px;
    margin: 0;
    padding: 30px 0;
    font-weight: normal;
}

.location__link {
    align-self: center;
    max-height: 800px;
    width: 100%;
    object-fit: scale-down;
    position: relative;
    transition: opacity .3s linear;
    opacity: .9;
}

.location__link:hover {
    opacity: 1;
}



.location__—Åontacts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 20px;
    font-size: 40px;
    gap: 0 10%;
    text-align: center;
}

.contacts__title {
    max-width: 900px;
    white-space: pre-line;
}

.contacts__name {
    margin-bottom: 0;
}

.contacts__tel {
    text-decoration: none;
    color: white;
    transition: opacity .3s linear;
}

.contacts__tel:hover {
    opacity: .6;
    color: #059f19;
}

.location__map {
    margin-top: 50px;
    background-color: #4157A2;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    gap: 0 70px;
    padding: 25px 20px 0 20px;
}

.location__frame {
    width: 50vw;
    height: 50vw;
}

.map__text {
    font-size: 40px;
    max-width: 600px;

}

.map__title {
    margin-top: 0;
    text-align: center;
    font-weight: normal;
}

.map__navi {
    font-size: 27px;
}

@media screen and (max-width: 1100px) {
    .main__text {
        font-size: 25px;
    }

    .map__navi {
        font-size: 25px;
    }
}

@media screen and (max-width: 940px){
    .location__title {
        font-size: 30px;
    }

    .map__title {
        font-size: 30px;
    }

    .map__address {
        font-size: 30px;
    }

    .contacts__title {
        font-size: 30px;
    }
}

@media screen and (max-width: 900px) {
    .contacts__title {
        font-size: 25px;
    }

    .map__title {
        font-size: 25px;
    }

    .map__address {
        font-size: 25px;
    }

    .contacts__tel {
        font-size: 20px;
    }
    .contacts__name {
        font-size: 20px;
    }
}

@media screen and (max-width: 630px) {
    .location__map {
        grid-template-columns: 1fr;
    }

    .location__frame {
        width: 100%;
    }

    .map__title {
        margin-top: 20px;
    }

}

@media screen and (max-width: 440px) {
    .contacts__title {
        font-size: 20px;
    }

    .map__title {
        font-size: 20px;
    }

    .map__address {
        font-size: 15px;
    }

    .map__navi {
        font-size: 15px
    }

    .contacts__tel {
        font-size: 17px;
    }
    .contacts__name {
        font-size: 17px;
    }
}

@media screen and (max-width: 360px) {
    .contacts__title {
        font-size: 15px;
    }

    .map__title {
        font-size: 20px;
    }

    .map__address {
        font-size: 15px;
    }

    .contacts__tel {
        font-size: 15px;
    }
    .contacts__name {
        font-size: 15px;
    }
}
