.dressCode{
    display: flex;
    flex-direction: column;
    margin-top: -70px;
}

.dressCode__row {
    display: flex;
    min-height: 160px;
    width: 100%;
    background-color: #4157A2;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.dressCode__title {
    font-weight: normal;
    font-size: 40px;
}

.dressCode__info {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}

.dressCode__text {
    margin: 0 20px;
    font-size: 30px;
    max-width: 950px;
    white-space: pre-line;
    word-spacing: 3px;
}

.dressCode__colors {
    display: flex;
    flex-direction: column;
    align-items: center;
    /*margin: 0 50px;*/
}

.colors__title {
    font-weight: normal;
    font-size: 33px;
}

.colors__list {
    display: flex;
    gap: 0 5%;
    list-style: none;
    max-width: 950px;
    justify-content: center;
    padding: 0;
    margin: 0 20px;
}


@media screen and (max-width: 1100px) {
    .dressCode__text {
        font-size: 25px;
    }
}

@media screen and (max-width: 900px) {
    .dressCode__title {
        font-size: 30px;
    }

    .dressCode__text {
        font-size: 20px;
    }

    .colors__title {
        font-size: 25px;
    }
}

@media screen and (max-width: 440px) {
    .dressCode__title {
        font-size: 25px;
    }

    .dressCode__text {
        font-size: 17px;
    }

    .colors__title {
        font-size: 20px;
    }
}

@media screen and (max-width: 360px) {
    .dressCode__title {
        font-size: 20px;
    }

    .dressCode__text {
        font-size: 15px;
    }

    .colors__title {
        font-size: 15px;
    }
}
