.main{
    display: flex;
    flex-direction: column;
    max-width: 1440px;
    margin: 0 auto;
    align-items: center;
}

.main__info {
    max-width: 1440px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 20%;
    align-items: center;
}

.main__text {
    display: block;
    max-width: 800px;
    text-align: center;
    white-space: pre-line;
    font-size: 30px;
    padding-left: 30px;
    z-index: 1;
}

.main__decor {
    max-width: 1380px;
    align-self: flex-end;
}




@media screen and (max-width: 1100px) {
    .main__text {
        font-size: 25px;
    }
}

@media screen and (max-width: 900px) {
    .main__text {
        font-size: 20px;
    }
}

@media screen and (max-width: 800px) {
    .main__info {
        gap: 0 10%;
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 440px) {
    .main__text {
        font-size: 17px;
    }
}

@media screen and (max-width: 360px) {
    .main__text {
        font-size: 15px;
    }
}


